import React, { useRef, useEffect } from "react";
import gsap from "gsap";
import { personalDetails } from "../Details";
import { useMediaQuery } from "@mui/material";
import './Main.css'
import DownloadIcon from '@mui/icons-material/Download';


function Home() {
  const { name, tagline, img, img2, img3, img4, img5,img6,img7 } = personalDetails;

  const Mobile = useMediaQuery('(max-width:450px)');
  const Tablet = useMediaQuery('(max-width:768px)');
  const Laptop = useMediaQuery('(max-width:992px)');
  const Desktop = useMediaQuery('(max-width:1200px)');

  const handleDownload = () => {
    // Create a link element
    const link = document.createElement('a');
    link.href = process.env.PUBLIC_URL + '/resume.pdf'; // Replace 'your-resume.pdf' with your actual PDF file name
    link.download = 'Resume-Asadulhaq.pdf'; // The name to save the downloaded file as

    // Trigger a click event on the link to initiate the download
    link.click();
  };

  const h11 = useRef();
  const h12 = useRef();
  const h13 = useRef();
  const myimageref = useRef();
  const h14 = useRef();
  useEffect(() => {
    const tl = gsap.timeline();
    tl.from(
      h11.current,
      {
        y: "-100%",
        delay: 0.3,
        opacity: 0,
        duration: 2,
        ease: "Power3.easeOut",
      },
      "<"
    )
      .from(
        h12.current,
        {
          x: "-100%",
          delay: 0.3,
          opacity: 0,
          duration: 2,
          ease: "Power3.easeOut",
        },
        "<"
      )
      .from(
        h13.current,
        {
          y: "100%",
          delay: 0.1,
          opacity: 0,
          duration: 2,
          ease: "Power3.easeOut",
        },
        "<"
      )
      .from(
        myimageref.current,
        {
          x: "200%",
          delay: 0.3,
          opacity: 0,
          duration: 2,
          ease: "Power3.easeOut",
        },
        "<"
      ).from
      (h14.current,
        {
          y: "-100%",
          delay: 0.3,
          opacity: 0,
          duration: 2,
          ease: "Power3.easeOut",
        },
        "<"
      );

  }, []);

  return (
    <>

      <div id="home" className="container" style={{ marginTop: "80px" }}>
        <div className="row">


          <div className={`col-md-6 ${Mobile ? 'mx-auto' : ''}`}>
            <img ref={myimageref} className="img-fluid" style={{ objectFit: "cover"  }} src={img7} alt="Asad" />
          </div>
          <div className={`col-md-6 p-3 ${Mobile ? 'mx-auto' : ''} my-auto`}>
            <h1
              ref={h11}
              className="text-2xl text-dark-heading md:text-4xl xl:text-5xl xl:leading-tight font-bold"
            >
              Hey,👋 My Name is {name}
            </h1>
            {/* <h1
            ref={h12}
            className="text-2xl bg-clip-text bg-gradient md:text-4xl xl:text-5xl xl:leading-tight font-bold"
          >
            {name}
          </h1> */}
            <h2
              ref={h13}
              className="text-2xl text-dark-heading md:text-4xl xl:text-5xl xl:leading-tight font-bold"
            >
              {tagline}
            </h2>
            <div ref={h14} className={`mt-3 d-flex flex-row ${Mobile ? "justify-content-center" : ""}`}>
              <button onClick={handleDownload} className={`border ${Mobile ? "" : "ms-2"} `}>
                <DownloadIcon className="me-3" />
                My Resume
              </button>
            </div>
          </div>
        </div>
      </div>

    </>
  );
}

export default Home;
