import React, { useState } from "react";
import { contactDetails } from "../Details";
import { useForm, ValidationError } from '@formspree/react';
import './Main.css';
import { useMediaQuery } from "@mui/material";



function Contact() {

  const { emaill, phone } = contactDetails;

  const messagee = encodeURIComponent('Hey, I was just checking out your profile . Let"s have a chat?'); // Replace with your desired message
  const number=+923363679562;

  const openWhatsApp = () => {
    const whatsappURL = `https://wa.me/${number}?text=${messagee}`;
    window.open(whatsappURL, '_blank');
  };


  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [message, setMessage] = useState("");
  const Mobile = useMediaQuery('(max-width:450px)');

  const [state, handleSubmit] = useForm("xdorpaaa");
  if (state.succeeded) {
    return <div className=" mainbg" id="contact">
      <h1 className="text-2xl pt-10 text-light text-center md:text-4xl xl:text-5xl xl:leading-tight font-bold">Contact</h1>
      <div className="row mt-5 ps-5 pe-5 pb-5">
        <div className="col-md-6 col-sm-12 my-uuto ">
          <main id="contact" className="">
            <h1 className="text-center fs-2 text-light font-semibold md:font-bold">
              For any questions please drop a mail
            </h1>
            <h3 className={`text-gradient ${Mobile ? 'fs-5' : 'fs-2 text-center'} pt-2 text-wrap font-semibold md:font-bold `}>
              <a href={`mailto:${emaill}`}>{emaill}</a>
            </h3>
            <span className="text-center text-content text-xl pt-1 font-light block">or</span>
            <h3 className="text-center fs-2 text-gradient py-2 font-semibold md:font-bold pt-2 md:py-6">
                <a href={`tel:${phone}`}>{phone}</a>
              </h3>
          </main>
        </div>
        <div className="col-md-6 col-sm-12 ">
          <form className="contact-us-form d-flex flex-column" onSubmit={handleSubmit}>
            <div class="form-outline mb-3">
              <input type="text" disabled={true}
                onChange={(e) => setName(e.target.value)} placeholder="Enter Name" id="typeText" class="form-control" />
            </div>
            <div class="form-outline my-3">
              <input type="email" disabled={true}
                onChange={(e) => setEmail(e.target.value)} placeholder="Enter Email" id="email" name="email" class="form-control" />
              <ValidationError
                prefix="Email"
                field="email"
                errors={state.errors}
              />
            </div>
            <div class="form-outline my-3">
              <textarea class="form-control" disabled={true}
                onChange={(e) => setMessage(e.target.value)} placeholder="Your Message" id="message" name="message" rows="4"></textarea>
              <ValidationError
                prefix="Message"
                field="message"
                errors={state.errors}
              />
            </div>
            <button disabled={true} style={{ width: "160px", height: "auto" }} className="border">Submitted!</button>
          </form>


        </div>
      </div>
    </div>;
  }

  return (
    <>
      <div className=" mainbg" id="contact">
        <h1 className="text-2xl pt-10 text-light text-center md:text-4xl xl:text-5xl xl:leading-tight font-bold">Contact</h1>
        <div className="row mt-5 ps-5 pe-5 pb-5">
          <div className="col-md-6 col-sm-12 my-uuto ">
            <main id="contact" className="">
              <h1 className="text-center fs-2 text-light font-semibold md:font-bold">
                For any questions please drop a mail
              </h1>
              <h3 className={`text-gradient ${Mobile ? 'fs-5' : 'fs-2 text-center'} pt-2 text-wrap font-semibold md:font-bold `}>
                <a href={`mailto:${emaill}`}>{emaill}</a>
              </h3>
              <span className="text-center text-content text-xl pt-1 font-light block">or</span>
              <h3 className="text-center fs-2 text-gradient py-2 font-semibold md:font-bold pt-2 md:py-6">
                <a onClick={openWhatsApp} style={{cursor:"pointer"}} >{phone}</a>
              </h3>
            </main>
          </div>
          <div className="col-md-6 col-sm-12 ">
            <form className="contact-us-form d-flex flex-column" onSubmit={handleSubmit}>
              <div class="form-outline mb-3">
                <input type="text" value={name}
                  onChange={(e) => setName(e.target.value)} placeholder="Enter Name" id="typeText" class="form-control" />
              </div>
              <div class="form-outline my-3">
                <input type="email" value={email}
                  onChange={(e) => setEmail(e.target.value)} placeholder="Enter Email" id="email" name="email" class="form-control" />
                <ValidationError
                  prefix="Email"
                  field="email"
                  errors={state.errors}
                />
              </div>
              <div class="form-outline my-3">
                <textarea class="form-control" value={message}
                  onChange={(e) => setMessage(e.target.value)} placeholder="Your Message" id="message" name="message" rows="4"></textarea>
                <ValidationError
                  prefix="Message"
                  field="message"
                  errors={state.errors}
                />
              </div>
              <button disabled={state.submitting} style={{ width: "160px", height: "auto" }} className="border">Send</button>
            </form>


          </div>
        </div>
      </div>

    </>
  );
}

export default Contact;
