import React from 'react'
import style from './NotFound.module.css'
import { Link } from 'react-router-dom'
import not from '../assets/nopreview.jpg'

const NotFound = () => {
    return (
        <div className={`${style.bgimg}`}>
            <img src={not} className={`${style.imgB} img-fluid`} alt="" />
            <Link to='/'>
                <button className={`${style.btnn} mt-5`}>Go Back</button>
            </Link>
        </div>
    )
}

export default NotFound
