import React from "react";
import { techStackDetails } from "../Details";

function Technologies() {
  const {
    html,
    css,
    js,
    react,
    redux,
    tailwind,
    bootstrap,
    sass,
    vscode,
    git,
    github,
    npm,
    postman,
    figma,
    express,
    graphql,
    postgres,
    nodejs,
    django,
    solidity,
    py,
    aws,
    gcd
  } = techStackDetails;
  return (
    <main id="technologies" className="container mx-auto pt-10 pb-20 ">
      <section>
        <h1 className="text-2xl text-dark-heading text-center md:text-4xl xl:text-5xl xl:leading-tight font-bold">
          Tech Stack
        </h1>
        <p className="text-content py-2 lg:max-w-3xl">
          Technologies I've been working with recently
        </p>
      </section>
      <section className="grid grid-cols-4 md:grid-cols-5 lg:grid-cols-6 items-center gap-10 pt-6">
        <img src={html} title="html" alt="" />
        <img src={css} title="CSS" alt="" />
        <img src={js} title="JavaScript" alt="" />
        <img src={react} title="React" alt="" />
        <img src={redux} title="Redux" alt="" />
        <img src={tailwind} title="Tailwind CSS" alt="" />
        <img src={bootstrap} title="Bootstrap" alt="" />
        <img src={sass} title="SASS" alt="" />
        <img src={django} title="Django" alt="" />
        <img src={express} title="Express" alt="" />
        <img src={graphql} title="GraphQL" alt="" />
        <img src={nodejs} title="NodeJS" alt="" />
        <img src={py} title="Python" alt="" />
      </section>
      <section>
        <h1 className="text-2xl pt-10 text-dark-heading text-center md:text-4xl xl:text-5xl xl:leading-tight font-bold">
          Tools
        </h1>
      </section>
      <section className="grid grid-cols-4 md:grid-cols-5 lg:grid-cols-6 items-center gap-10 pt-6">
        <img src={vscode} title="Visual Studio Code" alt="" />
        <img src={git} title="Git" alt="Git" />
        <img src={github} title="Github" alt="Github" />
        <img src={figma} title="Figma" alt="Figma" />
        <img src={npm} title="NPM" alt="NPM" />
        <img src={postman} title="Postman" alt="Postman" />
        <img src={postgres} title="Postman" alt="Postgresql" />
        <img src={solidity} title="Postman" alt="Solidity" />
        <img src={aws} title="AWS" alt="Solidity" />
        <img src={gcd} title="GCD" alt="Solidity" />
      </section>
    </main>
  );
}

export default Technologies;
