import React from "react";
import Work from "../Components/Work";
import { personalDetails, workDetails, eduDetails } from "../Details";
import { useMediaQuery } from "@mui/material";

function About() {

  const Mobile = useMediaQuery('(max-width:450px)');
  return (
    <main id="about" className="container mx-auto pt-10 pb-20 ">
      <h1 class="text-2xl md:text-4xl xl:text-5xl xl:leading-tight font-bold text-center md:text-left">
        About Me
      </h1>
      <section class="flex flex-col md:flex-row items-start md:items-center">
        <div class="md:w-1/2 p-4">
          <p class="text-content py-8">{personalDetails.about}</p>
        </div>
        <div class="md:w-1/2 mx-auto mb-4 flex justify-around">
          <div class="card shadow me-1" style={Mobile ? { height: "130px", width: "150px" } : { height: "170px", width: "230px" }}>
            <img src={personalDetails.exp} className="mx-auto my-auto" style={{ height: "50px", width: "50px" }} alt="" />
            <span class="fs-6 text-center">{Mobile ? 'Experience' : 'Professional Experience'}  </span>
            <h4 className="text-center "> 1 Year+</h4>
          </div>
          <div class="card shadow my-auto ms-1" style={Mobile ? { height: "130px", width: "150px" } : { height: "170px", width: "230px" }}>
            <img src={personalDetails.proj} className="mx-auto my-auto" style={{ height: "50px", width: "50px" }} alt="" />
            <span class="fs-6 text-center">{Mobile ? 'Projects' : 'Successful Projects'}   </span>
            <h4 className="text-center">3</h4>
          </div>
        </div>
      </section>

      <section>
        <h1 className="text-2xl text-dark-heading text-center md:text-4xl xl:text-5xl xl:leading-tight font-bold">
          Work Experience
        </h1>
        {React.Children.toArray(
          workDetails.map(({ Position, Company, Location, Type, Duration, img }) => (
            <Work
              position={Position}
              company={Company}
              location={Location}
              type={Type}
              duration={Duration}
              img={img}
            />
          ))
        )}
      </section>
      <section>
        <h1 className="text-2xl pt-10 text-dark-heading text-center md:text-4xl xl:text-5xl xl:leading-tight font-bold">
          Education
        </h1>
        {React.Children.toArray(
          eduDetails.map(({ Position, Company, Location, Type, Duration, img }) => (
            <Work
              position={Position}
              company={Company}
              location={Location}
              type={Type}
              duration={Duration}
              img={img}
            />
          ))
        )}
      </section>
    </main>
  );
}

export default About;
