import { BrowserRouter as Router, Routes, Route } from "react-router-dom";
import Main from "./Pages/Main";
import NotFound from "./Pages/NotFound";


function App() {
  return (
    <Router>
      <Routes>
        <Route path="/" element={<Main />} />
        <Route path="/notfound" element={<NotFound />} />
      </Routes>
    </Router>
  );
}
export default App;
