import React from 'react'
import style from './Services.module.css';
import ui from '../assets/techstack/react.png'
import backend from '../assets/django-back.png'
import full from '../assets/full-stack.png'
import { useMediaQuery } from '@mui/material';

const Services = () => {
  const mobile = useMediaQuery('(max-width:450px)');
  return (
    <div className='container-fluid' id='services'>
      <h1 className='text-2xl text-dark-heading text-center md:text-4xl xl:text-5xl xl:leading-tight font-bold'>Services</h1>
      <section class={`${style.container}`}>

        <section class={`${style.card__container}`}>
          <div class={`${style.card__bx} ${style.card__1}`} style={mobile ? { width: "300px" } : { width: "400px" }}>
            <div class={`${style.card__data}`}>
              <div class={`${style.card__icon}`}>
                <div class={`${style.card__icon_bx}`}>
                  <img src={ui} alt="" />
                </div>
              </div>
              <div class={`${style.card__content}`}>
                <h3>FrontEnd Design</h3>
                <p>Fully responsive frontend using latest frameworks like ReactJs , NextJs etc.</p>
                {/* <a href="#">Read More</a> */}
              </div>
            </div>
          </div>
          <div class={`${style.card__bx} ${style.card__2}`} style={mobile?{ width:"300px" }:{width:"400px"}}>
            <div class={`${style.card__data}`}>
              <div class={`${style.card__icon}`}>
                <div class={`${style.card__icon_bx}`}>
                  <img src={backend} alt="" />
                </div>
              </div>
              <div class={`${style.card__content}`}>
                <h3>Backend End</h3>
                <p>Backend Design from schema design to database management using powerfull frameworks like Python Django and NodeJs etc.</p>
                {/* <a href="#">Read More</a> */}
              </div>
            </div>
          </div>
          <div class={`${style.card__bx} ${style.card__3}`} style={mobile?{ width:"300px" }:{width:"400px"}}>
            <div class={`${style.card__data}`}>
              <div class={`${style.card__icon}`}>
                <div class={`${style.card__icon_bx}`}>
                  <img src={full} alt="" />
                </div>
              </div>
              <div class={`${style.card__content}`}>
                <h3>Full Stack Development</h3>
                <p>Building the website from scratch from designing the frontend to backend using powerfull and latest Stack.</p>
                {/* <a href="#">Read More</a> */}
              </div>
            </div>
          </div>

        </section>

      </section>
    </div>
  )
}

export default Services

